const nameReg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/

export default {
  name(value) {
    if (!value) {
      return ''
    }

    if (!nameReg.test(value) || value.length > 20) {
      return '"_"20'
    }
  },

  templateId(value) {
    if (!value) {
      return ''
    }
  },

  site(value) {
    if (!value) {
      return ''
    }
  },

  shop(value) {
    if (!value) {
      return ''
    }
  },

  operation(value) {
    if (!value) {
      return ''
    }
  },

  products(value) {
    if (!value[0]) {
      return ''
    }
  }
}
